import React from "react";
import { HashRouter, Routes, Route,} from "react-router-dom";
import Home from "../pages/home/HomeComponent.js";
import Splash from "../pages/splash/Splash";
 import Education from "../pages/education/EducationComponent";
import Experience from "../pages/experience/Experience";
import Contact from "../pages/contact/ContactComponent";
import Projects from "../pages/projects/Projects";
import { settings } from "../portfolio.js"; 
import NotFound from "../pages/notFound/NotFound.js";
import Tools from "../pages/tools/Tools.js";

export default function Main(props) {
  if (settings.isSplash) {
    return (
      <div>
        <HashRouter>
          <Routes>
          <Route path="/" exact     element={<Splash theme={props.theme} setTheme={props.setTheme} />} />
          <Route path="/splash"     element={<Splash theme={props.theme} setTheme={props.setTheme} />} />
          <Route path="/home"       element={<Home theme={props.theme} setTheme={props.setTheme} />} />
          <Route path="/experience" element={<Experience theme={props.theme} setTheme={props.setTheme} />} />
          <Route path="/education"  element={<Education theme={props.theme} setTheme={props.setTheme} />} /> 
          <Route path="/contact"    element={<Contact theme={props.theme} setTheme={props.setTheme} />} />
          <Route path="/projects"   element={<Projects theme={props.theme} setTheme={props.setTheme} />} />
          <Route path="/tools"      element={<Tools theme={props.theme} setTheme={props.setTheme} />} />
          <Route path="*"           element={<NotFound theme={props.theme} setTheme={props.setTheme} />} />
          </Routes>
        </HashRouter>
      </div>
    );
  } else {
    return (
      <div>
        <HashRouter>
          <Routes>
            <Route path="/" exact     element={<Home theme={props.theme} setTheme={props.setTheme} />} />
            <Route path="/splash"     element={<Splash theme={props.theme} setTheme={props.setTheme} />} />
            <Route path="/home"       element={<Home theme={props.theme} setTheme={props.setTheme} />} />
            <Route path="/experience" element={<Experience theme={props.theme} setTheme={props.setTheme} />} />
            <Route path="/education"  element={<Education theme={props.theme} setTheme={props.setTheme} />} /> 
            <Route path="/contact"    element={<Contact theme={props.theme} setTheme={props.setTheme} />} />
            <Route path="/projects"   element={<Projects theme={props.theme} setTheme={props.setTheme} />} />
            <Route path="/tools"      element={<Tools theme={props.theme} setTheme={props.setTheme} />} />
            <Route path="*"           element={<NotFound theme={props.theme} setTheme={props.setTheme} />} />
          </Routes>
        </HashRouter>
      </div>
    );
  }
}
