import React from "react";
import "./SoftwareSkill.css";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

function SoftwareSkill(props) {
  return (
    <div>
      <div className="software-skills-main-div">
        <ul className="dev-icons">
          {props.logos.map((logo) => {
            return (
              <OverlayTrigger
                key={logo.skillName}
                placement={"top"}
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id={`tooltip-top`}>
                    <strong>{logo.skillName}</strong>
                  </Tooltip>
                }

                  // ------Tooltips flickering when it's text wraps due to being positioned near right side of viewport--------
              // popperConfig={{
              //   modifiers: {
              //     preventOverflow: {
              //       enabled: false
              //     }
              //   }
              // }}
              // __________have to modify it, If we use newer version of react-bootstrap_____________
              >
                <li className="software-skill-inline" name={logo.skillName}>
                  <span
                    className="iconify"
                    data-icon={logo.fontAwesomeClassName}
                    style={logo.style}
                    data-inline="true"
                  ></span>
                </li>
              </OverlayTrigger>
            );
          })}
        </ul>
      </div>
    </div>
  );
}

export default SoftwareSkill;
